        '/assets/img/demo/ichigo/9.webp',
        '/assets/img/demo/jdn/5.webp',
        '/assets/img/demo/laak/10.webp',
        '/assets/img/demo/lass/3.webp',
        '/assets/img/demo/lenhy/14.webp',
        '/assets/img/demo/lionnax/6.webp',
        '/assets/img/demo/nice-life/7.webp',
        '/assets/img/demo/noos/11.webp',
        '/assets/img/demo/paraiso/1.webp',
